
<template>
        <div class="card" style="max-height: 435px;min-height: 435px;">
          <div class="card-body">
            <h4 class="card-title">F.S.T. Pendientess</h4>
            <div class="row mt-4">
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_length" class="dataTables_length">
                  <label class="d-inline-flex align-items-center">
                    Show&nbsp;
                    <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
                  </label>
                </div>
              </div>
              
              <!-- Search -->
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_filter" class="dataTables_filter text-md-right">
                  <label class="d-inline-flex align-items-center">
                    Search:
                    <b-form-input
                      v-model="filter"
                      type="search"
                      placeholder="Search..."
                      class="form-control form-control-sm ml-2"
                    ></b-form-input>
                  </label>
                </div>
              </div>
              <!-- End search -->
            </div>
            <!-- Table -->
            <div class="table-responsive mb-0">
              <b-table
                :items="fst"
                :fields="fields"
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
                <template v-slot:cell(TECNICO)="data">
                  {{data.item.Tecnico.nombre}} {{data.item.Tecnico.apellido}}
                </template>
                <template v-slot:cell(ENTIDAD)="data">
                  {{data.item.entidad.empresa}}
                </template>
                <template v-slot:cell(CONSECUTIVO)="data">
                  ({{data.item.consecutivo}}) {{data.item.programacion_ath.tipo_llamada}}-{{data.item.programacion_ath.llamada}}
                </template>
                <template v-slot:cell(VALOR)="data">
                  {{data.item.valor | moneda}}
                </template>
                <template v-slot:cell(ESTADO)="data">
                   
                  <b-badge variant="warning" v-if="data.item.status==='Pendiente'">{{data.item.status}}</b-badge>
                  <b-badge variant="success" v-if="data.item.status==='Aprobado'">{{data.item.status}}</b-badge>
                  <b-badge variant="danger" v-if="data.item.status==='Rechazado'">{{data.item.status}}</b-badge>
                </template>
                <template v-slot:cell(actions)="data" v-if="!usuarioDB.rol==='Tecnico'">

                <b-dropdown size="sm" class="">
                  <template v-slot:button-content>
                    Action
                    <i class="mdi mdi-chevron-down"></i>
                  </template>
                        <b-dropdown-item-button ><a :href="'llamada_ath_tablero/'+data.item.id_programacion" style="color:#000">Ver</a></b-dropdown-item-button>
                </b-dropdown>
                </template>
              </b-table>
            </div>
            <div class="row">
              <div class="col">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
</template>

<script>

import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
/**
 * Transactions component
 */
export default {
  data() {
    return {
      fst: [],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 25, 50],
      filter: null,
      filterOn: [],
      sortBy: "orderid",
      sortDesc: false,
      fields: ["TECNICO","ENTIDAD","CONSECUTIVO","VALOR","ESTADO","actions"],
    };
  },
  computed: {
    /**...mapState(['usuarioDB']),
     * Total no. of records
     */
    ...mapState(['usuarioDB']),
    rows() {
      return this.fst.length;
     
    }
  },
filters: {
      fecha: function (value) {
         moment.locale('es');
        let fechafina =moment(value).format('MMMM DD YYYY HH:MM')
           return fechafina
        },
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 0
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        }
  },
  mounted() {
      
      this.listarfst();
    // Set the initial number of items
    this.totalRows = this.fst.length;
  },
  methods: {
    /**
     * Search the table data with search input
     */
    async  listarfst(){
        let url= "";
        if (this.usuarioDB.rol==='Administrador'||this.usuarioDB.rol==='Root') {
            let data = new FormData();
            await  this.axios.post('api/formatos/find/dashboard/admin',data)
                .then((response) => {
                    this.fst = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
        if (this.usuarioDB.rol==='Analista') {
            let data = new FormData();
            await  this.axios.post('api/formatos/find/dashboard/analista',data)
                .then((response) => {
                    this.fst = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
        if (this.usuarioDB.rol==='Tecnico') {
            let data = new FormData();
            await  this.axios.post('api/formatos/find/dashboard/tecnico',data)
                .then((response) => {
                    this.fst = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
      },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>
