
  <template>
        <div class="card" style="max-height: 500px;min-height: 560px;">
          <div class="card-body">
            <h4 class="card-title">Ultimas gestiones realizadas</h4>
            <div class="row mt-4">
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_length" class="dataTables_length">
                  <label class="d-inline-flex align-items-center">
                    Show&nbsp;
                    <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
                  </label>
                </div>
              </div>
              
              <!-- Search -->
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_filter" class="dataTables_filter text-md-right">
                  <label class="d-inline-flex align-items-center">
                    Search:
                    <b-form-input
                      v-model="filter"
                      type="search"
                      placeholder="Search..."
                      class="form-control form-control-sm ml-2"
                    ></b-form-input>
                  </label>
                </div>
              </div>
              <!-- End search -->
            </div>
            <!-- Table -->
            <div class="table-responsive mb-0">
              <b-table
                :items="gestion"
                :fields="fields"
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
                <template v-slot:cell(consecutivo)="data">
                    (ATH-{{data.item.id_programacion}}) {{data.item.programacion_ath.tipo_llamada}}-{{data.item.programacion_ath.llamada}}
                </template>
                <template v-slot:cell(status)="data">

                    <span class="badge badge-success" v-if="data.item.status==='Aceptada'">Aceptada</span>
                    <span class="badge badge-danger" v-if="data.item.status==='Devuelta'">Devuelta</span>
                    <span class="badge badge-warning" v-if="data.item.status==='Pendiente'">Pndiente</span>
                </template>
                <template v-slot:cell(tecnico)="data">
                    {{data.item.programacion_ath.Tecnico_ath.nombre}} {{data.item.programacion_ath.Tecnico_ath.apellido}}
                </template>
                <template v-slot:cell(analista)="data">
                    {{data.item.programacion_ath.Analista_ath.nombre}} {{data.item.programacion_ath.Analista_ath.apellido}}
                </template>
                <template v-slot:cell(fecha)="data">
                    {{data.item.created_at | fecha}}
                </template>
                <template v-slot:cell(valoracion)="data">
                  <div v-if="data.item.programacion_ath.valoracion">
                    {{data.item.programacion_ath.valoracion}} <i class="ri-star-line"></i>
                  </div>
                  <div ></div>
                </template>
                <template v-slot:cell(actions)="data">

                <b-dropdown size="sm" class="">
                  <template v-slot:button-content>
                    Action
                    <i class="mdi mdi-chevron-down"></i>
                  </template>
                        <b-dropdown-item-button ><a :href="'llamada_ath_tablero/'+data.item.id_programacion" style="color:#000">Ver</a></b-dropdown-item-button>
                </b-dropdown>
                </template>
              </b-table>
            </div>
            <div class="row">
              <div class="col">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
</template>

<script>

import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
/**
 * Transactions component
 */
export default {
  data() {
    return {
      gestion: [],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 25, 50],
      filter: null,
      filterOn: [],
      sortBy: "orderid",
      sortDesc: false,
      fields: [ "consecutivo","status","tecnico",  "analista", "fecha", "valoracion","actions"]
    };
  },
  computed: {
    /**...mapState(['usuarioDB']),
     * Total no. of records
     */
    ...mapState(['usuarioDB']),
    rows() {
      return this.gestion.length;
     
    }
  },
filters: {
      fecha: function (value) {
         moment.locale('es');
        let fechafina =moment(value).format('MMMM DD YYYY HH:MM')
           return fechafina
        }
  },
  mounted() {
      
      this.listarGestion();
    // Set the initial number of items
    this.totalRows = this.gestion.length;
  },
  methods: {
    /**
     * Search the table data with search input
     */
    async  listarGestion(){
        let url= "";
        if (this.usuarioDB.rol==='Administrador'||this.usuarioDB.rol==='Root') {
            let data = new FormData();
            await  this.axios.post('api/gestion/ath/find/dashboard',data)
                .then((response) => {
                    this.gestion = response.data.rows;
                    for (let index = 0; index <this.gestion.length; index++) {
                    this.gestion[index].evidencias_antes=JSON.parse(this.gestion[index].evidencias_antes);
                    this.gestion[index].evidencias_durante=JSON.parse(this.gestion[index].evidencias_durante);
                    this.gestion[index].evidencias_despues=JSON.parse(this.gestion[index].evidencias_despues); 
                    }
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
        if (this.usuarioDB.rol==='Analista') {
            let data = new FormData();
            await  this.axios.post('api/gestion/ath/find/dashboard/analista',data)
                .then((response) => {
                    this.gestion = response.data.rows;
                    for (let index = 0; index <this.gestion.length; index++) {
                    this.gestion[index].evidencias_antes=JSON.parse(this.gestion[index].evidencias_antes);
                    this.gestion[index].evidencias_durante=JSON.parse(this.gestion[index].evidencias_durante);
                    this.gestion[index].evidencias_despues=JSON.parse(this.gestion[index].evidencias_despues); 
                    }
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
        if (this.usuarioDB.rol==='Tecnico') {
            let data = new FormData();
            await  this.axios.post('api/gestion/ath/find/dashboard/tecnico',data)
                .then((response) => {
                    this.gestion = response.data.rows;
                    for (let index = 0; index <this.gestion.length; index++) {
                    this.gestion[index].evidencias_antes=JSON.parse(this.gestion[index].evidencias_antes);
                    this.gestion[index].evidencias_durante=JSON.parse(this.gestion[index].evidencias_durante);
                    this.gestion[index].evidencias_despues=JSON.parse(this.gestion[index].evidencias_despues); 
                    }
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
      },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>
