<script>

import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
/**
 * Recent activity component
 */
export default {
  data() {
    return {
      programas: [],
    };
  },
  computed: {
  /**...mapState(['usuarioDB']),
   * Total no. of records
   */
  ...mapState(['usuarioDB']),
  },
  filters: {
        capitalize: function (value) {
          if (!value) return ''
          let fecha = moment();
            let dias =fecha.diff(value, 'days'); 
            let horas =fecha.diff(value, 'hours'); 
            let minutos =fecha.diff(value, 'minutes'); 
          if (dias>1) {
           return "hace " + dias + " dias."; 
          } if (dias== 0 && horas>0) {
               return "hace " + horas + " horas."; 
          } else {
               return "hace " + minutos + " minutos."; 
          }
        },
        restan: function (value) {
          if (!value) return ''
          let fecha = moment();

        if (fecha.diff(value, 'hours')>0) {
            return  value =-1 * fecha.diff(value, 'hours')+ " horas vencido."; 
          }else{
            return value =-1 * fecha.diff(value, 'hours')+ " horas para caducar."; 
          }
 
        },
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 1
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        },
        fecha: function (value) {
         moment.locale('es');
        let fechafina =moment(value).format('MMMM DD YYYY HH:MM')
           return fechafina
        }
    },
  methods:{
    async  listarProgramacion(){
        if (this.usuarioDB.rol==='Administrador'||this.usuarioDB.rol==='Root'||this.usuarioDB.rol==='Coordinador') {
            await  this.axios.get('/api/legalizaciones/ath/dashboard/administrador')
                .then((response) => {
                this.programas = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        if (this.usuarioDB.rol==='Analista') {
            await  this.axios.get('/api/legalizaciones/ath/dashboard/analista')
                .then((response) => {
                this.programas = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        if (this.usuarioDB.rol==='Tecnico') {
            await  this.axios.get('/api/legalizaciones/ath/dashboard/tecnico')
                .then((response) => {
                this.programas = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        
    },
  },
  created(){
    this.listarProgramacion();
  }
};
</script>

<template>
  <div class="card">
    <div class="card-body">

    <h4 class="card-title mb-4">Legalizaciones</h4>
    <div class="row">
     
    </div>
      <div data-simplebar style="max-height: 480px;min-height: 480px;">
        <ul class="list-unstyled activity-wid">
         
            <li class="activity-list" v-for="(data, index) in programas" :key="index">
                 <a :href="'llamada_ath_tablero/'+data.programacion_ath.id">
                <div class="activity-icon avatar-xs">
                <span class="avatar-title bg-soft-primary text-primary rounded-circle">
                    <i class="ri-check-line"></i>
                </span>
                </div>
                <div>
                <div>
                    <h5 class="font-size-13">
                   Legalizacin (ATH-{{data.programacion_ath.id}}) {{data.programacion_ath.tipo_llamada}}-{{data.programacion_ath.llamada}}
                    <small class="text-muted">{{data.created_at  | capitalize }}</small>
                    </h5>
                    <span v-if="data.status==='Pendiente'" class="badge badge-warning">Pendiente</span>
                    <span v-if="data.status==='Aceptada'" class="badge badge-success">Aceptada</span>
                    <span v-if="data.status==='Devuelta'" class="badge badge-danger">Devuelta</span>
                </div>
                <div>
                    <p class="text-muted mb-0">Excendente: {{data.excedente | moneda}}</p>
                    <p class="text-muted mb-0" v-if="data.excedente>0"> A favor de Pristel</p>
                    <p class="text-muted mb-0" v-if="data.excedente<0"> A favor del tecnico</p>
                </div>
                <div>
                    <p class="text-muted mb-0">Observaciones:{{data.observaciones}}</p>
                </div>

                </div>
                 </a>
            </li>
         
        </ul>
      </div>
    </div>
  </div>
</template>