
<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row">
      <div class="col-xl-7">
          <Servicios />
          <Fst />
      </div>
      <div class="col-xl-5">
        <Llamadas />
        <Abonos />
      </div>
    </div>
    <div class="row">
      <div class="col-lg-4">
         <Legalizaciones />
      </div>
      <div class="col-lg-4" v-if="usuarioDB.rol==='Root'||usuarioDB.rol==='Administrador'||usuarioDB.rol==='Coordinador'||usuarioDB.rol==='Analista'||usuarioDB.tipo_tecnico==='Contratista'">
       <Cuentas />
      </div>
      <div class="col-lg-4">
        <Sxp />
      </div>
    </div>
  </Layout>
</template>

<script>
import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import Fst from "./fst";
import Sxp from "./sxp";
import Abonos from "./abonos";
import Legalizaciones from "./legalizaciones";
import Cuentas from "./cuentasdecobro";
import Stat from "./widget";
import RevenueAnalytics from "./revenue";
import SalesAnalytics from "./sales-analytics";
import Servicios from "./servicios";
import Notificaciones from "./notificaciones";
import Llamadas from "./llamadas";
import Sources from "./sources";
import RecentActivity from "./recent-activity";
import RevenueLocation from "./revenue-location";
import Chat from "./chat";
import Transaction from './transaction';

/**
 * Dashboard component
 */
export default {
  components: {
    Layout,
    PageHeader,
    Stat,
    Fst,
    Sxp,
    Abonos,
    Cuentas,
    Legalizaciones,
    Servicios,
    Llamadas,
    RevenueAnalytics,
    SalesAnalytics,
    Notificaciones,
    Sources,
    RecentActivity,
    RevenueLocation,
    Chat,
    Transaction
  },
  data() {
    return {
      title: "Dashboard",
      items: [
        {
          text: "Pritel"
        },
        {
          text: "Dashboard",
          active: true
        }
      ]
    };
  },
  computed:{
     ...mapState(['usuarioDB']),
  },
  methods:{
    ...mapActions(['guardarUsuario']),
	session(){
		if (localStorage.getItem('token')) {
			const token=localStorage.getItem('token');
			this.guardarUsuario(token);
			
		}else{
		   this.$router.push({ name: '/login' });
		}
	}
  },
    created(){
		  this.session();
    },
};
</script>
