<script>

import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
/**
 * Recent activity component
 */
export default {
  data() {
    return {
      programas: [],
      sintecnico:0,
      contecnico:0,
      enprocesos:0,
    };
  },
  computed: {
  /**...mapState(['usuarioDB']),
   * Total no. of records
   */
  ...mapState(['usuarioDB']),
  },
  filters: {
        capitalize: function (value) {
          if (!value) return ''
          let fecha = moment();
            let dias =fecha.diff(value, 'days'); 
            let horas =fecha.diff(value, 'hours'); 
            let minutos =fecha.diff(value, 'minutes'); 
          if (dias>1) {
           return "hace " + dias + " dias."; 
          } if (dias== 0 && horas>0) {
               return "hace " + horas + " horas."; 
          } else {
               return "hace " + minutos + " minutos."; 
          }
        },
        restan: function (value) {
          if (!value) return ''
          let fecha = moment();

        if (fecha.diff(value, 'hours')>0) {
            return  value =-1 * fecha.diff(value, 'hours')+ " horas vencido."; 
          }else{
            return value =-1 * fecha.diff(value, 'hours')+ " horas para caducar."; 
          }
 
        },
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 1
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        },
        fecha: function (value) {
         moment.locale('es');
        let fechafina =moment(value).format('MMMM DD YYYY HH:MM')
           return fechafina
        }
    },
  methods:{
    async  listarProgramacion(){
        if (this.usuarioDB.rol==='Administrador'||this.usuarioDB.rol==='Root'||this.usuarioDB.rol==='Coordinador') {
            await  this.axios.get('/api/programacion/ath')
                .then((response) => {
                this.programas = response.data.rows;
                this.sintecnico=0; this.contecnico=0; this.enprocesos;
                    for (let index = 0; index < this.programas.length; index++) {
                        if (this.programas[index].Tecnico_ath) {
                        this.contecnico= this.contecnico+1
                        }
                        if (!this.programas[index].Tecnico_ath) {
                        this.sintecnico= this.sintecnico+1
                        }
                        if (this.programas[index].gestion_aths.length>0) {
                        this.enprocesos= this.enprocesos+1
                        }
                    }
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        if (this.usuarioDB.rol==='Analista') {
            let data = new FormData();
            data.append('id',this.$route.params.id);
            await  this.axios.post('/api/programacion/ath/listaranalista',data)
                .then((response) => {
                this.programas = response.data.rows;
                this.sintecnico=0; this.contecnico=0; this.enprocesos;
                    for (let index = 0; index < this.programas.length; index++) {
                        if (this.programas[index].Tecnico_ath) {
                        this.contecnico= this.contecnico+1
                        }
                        if (!this.programas[index].Tecnico_ath) {
                        this.sintecnico= this.sintecnico+1
                        }
                        if (this.programas[index].gestion_aths.length>0) {
                        this.enprocesos= this.enprocesos+1
                        }
                    }
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        if (this.usuarioDB.rol==='Tecnico') {
            await  this.axios.get('api/programacion/ath/servicios')
                .then((response) => {
                this.programas = response.data;
                this.sintecnico=0; this.contecnico=0; this.enprocesos;
                    for (let index = 0; index < this.programas.length; index++) {
                        if (this.programas[index].gestion_aths.length>0) {
                        this.enprocesos= this.enprocesos+1
                        console.log("tiene procesos");
                        }
                    }
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        
    },
  },
  created(){
    this.listarProgramacion();
  }
};
</script>

<template>
  <div class="card">
    <div class="card-body">

    <h4 class="card-title mb-4">LLamadas</h4>
    <div class="row">
        <div class="col-md-3">
            <div class="card">
                <div class="card-body">
                <div class="media">
                    <div class="media-body overflow-hidden">
                    <p class="text-truncate font-size-14 mb-2"></p>
                    <h4 class="mb-0 mt-0 text-center  p-0">{{programas.length}}</h4>
                    </div>
                    <div class="text-primary">
                    <i :class="` font-size-24`"></i>
                    </div>
                </div>
                </div>
                <div class="card-body border-top py-3">
                <div class="text-truncate">
                    <span class="badge badge-soft-success font-size-11">
                    <i class="mdi mdi-menu-right"></i>
                    </span>
                    <span class="text-muted ml-2">Total</span>
                </div>
                </div>
            </div>
        </div>
          <div class="col-md-3" v-if="usuarioDB.rol==='Administrador'||usuarioDB.rol==='Coordinador'||usuarioDB.rol==='Analista'||usuarioDB.rol==='Root'" >
            <div class="card">
                <div class="card-body">
                <div class="media">
                    <div class="media-body overflow-hidden">
                    <p class="text-truncate font-size-14 mb-2"></p>
                    <h4 class="mb-0 text-danger text-center"> {{sintecnico}}</h4>
                    </div>
                    <div class="text-primary">
                    <i :class="` font-size-24`"></i>
                    </div>
                </div>
                </div>
                <div class="card-body border-top py-3">
                <div class="text-truncate">

                    <span class="text-muted ml-0">Sin programar</span>
                </div>
                </div>
            </div>
        </div>
                <div class="col-md-3">
            <div class="card">
                <div class="card-body">
                <div class="media">
                    <div class="media-body overflow-hidden">
                    <p class="text-truncate font-size-14 mb-2"></p>
                    <h4 class="mb-0 text-info text-center">{{contecnico}}</h4>
                    </div>
                    <div class="text-primary">
                    <i :class="` font-size-24`"></i>
                    </div>
                </div>
                </div>
                <div class="card-body border-top py-3">
                <div class="text-truncate">

                    <span class="text-muted">Programadas</span>
                </div>
                </div>
            </div>
        </div>
        <div class="col-md-3">
            <div class="card">
                <div class="card-body">
                <div class="media">
                    <div class="media-body overflow-hidden">
                    <p class="text-truncate font-size-14 mb-2"></p>
                    <h4 class="mb-0 text-success text-center">{{enprocesos}}</h4>
                    </div>
                    <div class="text-primary">
                    <i :class="` font-size-24`"></i>
                    </div>
                </div>
                </div>
                <div class="card-body border-top py-3">
                <div class="text-truncate">
                    <span class="text-muted">En proceso</span>
                </div>
                </div>
            </div>
        </div>      
    </div>
      <div data-simplebar style="max-height: 330px;min-height: 330px;">
        <ul class="list-unstyled activity-wid">
         
            <li class="activity-list" v-for="(data, index) in programas" :key="index">
                 <a :href="'llamada_ath_tablero/'+data.id">
                <div class="activity-icon avatar-xs">
                <span class="avatar-title bg-soft-primary text-primary rounded-circle">
                    <i :class="`${data.icon}`"></i>
                </span>
                </div>
                <div>
                <div>
                    <h5 class="font-size-13">
                    (ATH-{{data.id}}) {{data.tipo_llamada}}-{{data.llamada}}
                    <small class="text-muted">{{data.created_at  | capitalize }}</small>
                    </h5>
                    <b-badge variant="warning" v-if="data.status==='Pendiente'" style="font-size:10px">Pendiente</b-badge>
                    <b-badge variant="info"    v-if="data.status==='Programada'" style="font-size:10px">Programada</b-badge>
                    <b-badge variant="info"    v-if="data.status==='Reprogramada'" style="font-size:10px">Reprogramada</b-badge>
                    <b-badge variant="dark"    v-if="data.status==='Escalada'" style="font-size:10px">Escalada</b-badge>
                    <b-badge variant="danger"  v-if="data.status==='Rechazada'" style="font-size:10px">Rechazada</b-badge>
                    <b-badge variant="primary" v-if="data.status==='Archivada'" style="font-size:10px">Archivada</b-badge>
                    <b-badge variant="primary" v-if="data.status==='Cerrada'" style="font-size:10px">Cerrada</b-badge>
                    <b-badge variant="success" v-if="data.status==='Aceptada'" style="font-size:07px">Aceptada</b-badge>
                    <b-badge variant="warning" v-if="data.status==='Devuelta'" style="font-size:10px">Devuelta</b-badge>
                    <b-badge variant="primary" v-if="data.status==='Creada'" style="font-size:10px">Creada</b-badge>
                </div>

                <div>
                    <p class="text-muted mb-0">{{data.descripcion}}</p>
                </div>
                <div>
                    <p class="text-muted mb-0">Vencimiento para ATH: {{data.fecha_vencimiento | restan }}</p>
                </div>
                <div>
                    <p class="text-muted mb-0">Vencimiento para el tcnico {{data.vencimiento_tecnico | restan }}</p>
                </div>
                <div v-if="data.Analista_ath">
                    <p class="text-muted mb-0"> <span class="badge bg-info text-white">Analista:</span> {{data.Analista_ath.nombre }} {{data.Analista_ath.apellido }}</p>
                </div>
                <div v-if="data.Tecnico_ath">
                    <p class="text-muted mb-0"> <span class="badge bg-info text-white">Tcnico:</span> {{data.Tecnico_ath.nombre }} {{data.Tecnico_ath.apellido }}</p>
                </div>
                <div v-else>
                    <p class="text-muted mb-0">Sin tcnico asignado</p>  
                </div>
                </div>
                 </a>
            </li>
         
        </ul>
      </div>
    </div>
  </div>
</template>