<script>

import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
/**
 * Recent activity component
 */
export default {
  data() {
    return {
      abonos: []
    };
  },
  filters: {
        capitalize: function (value) {
          if (!value) return ''
          let fecha = moment();
            let dias =fecha.diff(value, 'days'); 
            let horas =fecha.diff(value, 'hours'); 
            let minutos =fecha.diff(value, 'minutes'); 
          if (dias>1) {
           return "hace " + dias + " dias."; 
          } if (dias== 0 && horas>0) {
               return "hace " + horas + " horas."; 
          } else {
               return "hace " + minutos + " minutos."; 
          }
        },
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 0
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        }
    },
  methods:{
    async listarAbonos(){
        if (this.usuarioDB.rol==='Administrador'||this.usuarioDB.rol==='Root') {
            let data = new FormData();
            await  this.axios.get('api/abonos/dashboard/administrador',data)
                .then((response) => {
                    this.abonos = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
        if (this.usuarioDB.rol==='Analista') {
            let data = new FormData();
            await  this.axios.get('api/abonos/dashboard/analista',data)
                .then((response) => {
                    this.abonos = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
        if (this.usuarioDB.rol==='Tecnico') {
            await  this.axios.get('api/abonos/dashboard/tecnico')
                .then((response) => {
                    this.abonos = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
            })
        }
    },
  },
    computed: {
    /**...mapState(['usuarioDB']),
     * Total no. of records
     */
    ...mapState(['usuarioDB']),
    rows() {
      return this.gestion.length;
     
    }
  },
  created(){
    this.listarAbonos();
  }
};
</script>

<template>
  <div class="card" style="max-height: 435px;min-height: 435px;">
    <div class="card-body">
      <h4 class="card-title mb-4">ltimos abonos</h4>
      <div data-simplebar style="max-height: 330px;">
        <ul class="list-unstyled activity-wid">
          <li class="activity-list" v-for="(data, index) in abonos" :key="index">
            <div class="activity-icon avatar-xs">
              <span class="avatar-title bg-soft-primary text-primary rounded-circle">
                <i class="ri-money-dollar-circle-line"></i>
              </span>
            </div>
            <div>
              <div>
                <h5 class="font-size-13">
                  Abonos 
                  {{data.formato.programacion_ath.tipo_llamada}}-{{data.formato.programacion_ath.llamada}} (ATH-{{data.formato.programacion_ath.id}}) (F.S.T-{{data.formato_id}})
                  <small class="text-muted">{{data.created_at  | capitalize }}</small>
                </h5>
              </div>
              <div>
                <h5 class="font-size-13">
                  {{data.valor_abono | moneda}}
                </h5>
                <h6 class="font-size-13">
                  {{data.tipo}}
                </h6>
                <h6 class="font-size-13 text-muted">
                  Observaciones: {{data.descripcion_abono}}
                </h6>
                <a :href="data.archivo_abono" download>Soporte</a>
              </div>
              <div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>