<script>

import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
/**
 * Recent activity component
 */
export default {
  data() {
    return {
      programas: [],
    };
  },
  computed: {
  /**...mapState(['usuarioDB']),
   * Total no. of records
   */
  ...mapState(['usuarioDB']),
  },
  filters: {
        capitalize: function (value) {
          if (!value) return ''
          let fecha = moment();
            let dias =fecha.diff(value, 'days'); 
            let horas =fecha.diff(value, 'hours'); 
            let minutos =fecha.diff(value, 'minutes'); 
          if (dias>1) {
           return "hace " + dias + " dias."; 
          } if (dias== 0 && horas>0) {
               return "hace " + horas + " horas."; 
          } else {
               return "hace " + minutos + " minutos."; 
          }
        },
        restan: function (value) {
          if (!value) return ''
          let fecha = moment();

        if (fecha.diff(value, 'hours')>0) {
            return  value =-1 * fecha.diff(value, 'hours')+ " horas vencido."; 
          }else{
            return value =-1 * fecha.diff(value, 'hours')+ " horas para caducar."; 
          }
 
        },
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 1
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        },
        fecha: function (value) {
         moment.locale('es');
        let fechafina =moment(value).format('MMMM DD YYYY HH:MM')
           return fechafina
        }
    },
  methods:{
    async  listarProgramacion(){
        if (this.usuarioDB.rol==='Administrador'||this.usuarioDB.rol==='Root'||this.usuarioDB.rol==='Coordinador') {
            await  this.axios.get('/api/ath/formatodecobro/dashboard/administrador')
                .then((response) => {
                this.programas = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        if (this.usuarioDB.rol==='Analista') {
            await  this.axios.get('/api/ath/formatodecobro/dashboard/analista')
                .then((response) => {
                this.programas = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        if (this.usuarioDB.rol==='Tecnico') {
            await  this.axios.get('/api/ath/formatodecobro/dashboard/tecnico')
                .then((response) => {
                this.programas = response.data.rows;
                })
                .catch((e)=>{
                console.log('error' + e);
           });
        }
        
    },
  },
  created(){
    this.listarProgramacion();
  }
};
</script>

<template>
  <div class="card">
    <div class="card-body">

    <h4 class="card-title mb-4">Cuentas de cobro pendientes</h4>
    <div class="row">
     
    </div>
      <div data-simplebar style="max-height: 480px;min-height: 480px;">
        <ul class="list-unstyled activity-wid">
         
            <li class="activity-list" v-for="(data, index) in programas" :key="index">
                <div class="activity-icon avatar-xs">
                <span class="avatar-title bg-soft-primary text-primary rounded-circle">
                    <i class="ri-currency-fill"></i>
                </span>
                </div>
                <div>
                <div>
                    <h5 class="font-size-13">
                   Cuenta-{{data.id}}
                    <small class="text-muted">{{data.created_at  | capitalize }}</small>
                    </h5>
                  <b-badge variant="info"    v-if="data.status==='Creado'">    {{data.status}}</b-badge>
                  <b-badge variant="warning" v-if="data.status==='Pendiente'"> {{data.status}}</b-badge>
                   <b-badge variant="primary" v-if="data.status==='Revisado'">  {{data.status}}</b-badge>
                  <b-badge variant="success" v-if="data.status==='Aprobado'">  {{data.status}}</b-badge>
                  <b-badge variant="danger"  v-if="data.status==='Rechazado'"> {{data.status}}</b-badge>
                </div>
                <div>
                    <h6 class="">Tcnico: {{data.Tecnico_athc.nombre}} {{data.Tecnico_athc.apellido}}</h6>
                </div>
                <div>
                    <h5 class="mb-0 text-success text-right">Total: {{data.total | moneda}}</h5>
                </div>

                </div>
            </li>
         
        </ul>
      </div>
    </div>
  </div>
</template>