<script>

import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
/**
 * Recent activity component
 */
export default {
  data() {
    return {
      notificaciones: []
    };
  },
  filters: {
        capitalize: function (value) {
          if (!value) return ''
          let fecha = moment();
            let dias =fecha.diff(value, 'days'); 
            let horas =fecha.diff(value, 'hours'); 
            let minutos =fecha.diff(value, 'minutes'); 
          if (dias>1) {
           return "hace " + dias + " dias."; 
          } if (dias== 0 && horas>0) {
               return "hace " + horas + " horas."; 
          } else {
               return "hace " + minutos + " minutos."; 
          }
        }
    },
  methods:{
    async listarNotificaciones(index){
      await this.axios.get('api/notificacion/').then(response => {
        if (response.status==200) {
            this.notificaciones=response.data.rows;
          }
        }).catch(e => { 
      });
    },
  },
  created(){
    this.listarNotificaciones();
  }
};
</script>

<template>
  <div class="card">
    <div class="card-body">


      <h4 class="card-title mb-4">Ultimas notificaciones</h4>

      <div data-simplebar style="max-height: 330px;">
        <ul class="list-unstyled activity-wid">
          <li class="activity-list" v-for="(data, index) in notificaciones" :key="index">
            <div class="activity-icon avatar-xs">
              <span class="avatar-title bg-soft-primary text-primary rounded-circle">
                <i :class="`${data.icon}`"></i>
              </span>
            </div>
            <div>
              <div>
                <h5 class="font-size-13">
                  {{data.titulo}}
                  <small class="text-muted">{{data.created_at  | capitalize }}</small>
                </h5>
              </div>

              <div>
                <p class="text-muted mb-0">{{data.descripcion}}</p>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>